@import url(https://fonts.googleapis.com/css?family=Fugaz+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fugaz+One&display=swap);
button {
    padding: 0.5em 1em;
    border-radius: 2em;
    font-size: 0.8em;
    background: none;
    color: white;
    font-weight: bold;
}

.green-btn {
    background-image: var(--backgreen);
    background-size: 120% 120%;
    background-position: center;
    border: 2px solid transparent;
}

.blue-btn {
    background-color: #163E59;
    color: white;
    background-size: 120% 120%;
    background-position: center;
    border: none;
}

.valid-button {
    color: #021438;
    font-size: 0.8em;
    background-image: var(--backgreen);
    background-size: 120% 120%;
    background-position: center;
    border: 2px solid transparent;
    font-weight: bold;
}

#cta-btn {
    font-size: 1.3em;
    color: #021438;
}

.border-btn {
    /* border: 2px solid var(--lightgreen); */
    color: white;
    border: none;
    font-weight: lighter;
    font-size: 1.1em;
}

.see-more-btn {
    /* border-color: var(--darkblue); */
    color: var(--darkblue);
    line-height: 0.5em;
    font-size: 1.2em;
}

.left-btn {
    margin-left: 1em;
}

.right-btn {
    margin-right: 1em;
}

.down-btn {
    margin-bottom: 3rem;
    color: var(--lightgreen);
    font-size: 1em;
}

.up-btn {
    margin-top: 3em;
}
.icon-box{
    border: none;
}

.icon-gears{
    font-size: 3em;
}
/* /// SECTIONS */
.sim-page {
  height: 92vh;
  background-color: #e5e5e5;
  color: #163E59;
  padding: 10px;
  min-height: 500px;
}

.sim-container-box {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 60%;
  margin-right: 10px;
}

.sim-results-box {
  height: 100%;
  width: calc(40% - 10px);
}

/* /// LOADER */
#loader {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* /// SIM-NAV */
#sim-nav-box {
  align-items: center;
  height: 130px;
  padding: 10px;
  background-color: white;
  width: 100%;
  margin-bottom: 10px;
}


#sim-nav-box h1,
#results-top-box h1,
#results-emissions h1 {
  font-family: "Circular Std";
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 28px;
  color: #163e59;
  padding-bottom: 0;
}

#sim-nav-box h2 {
  font-family: "Circular Std";
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
}

#sim-nav-box>div {
  width: 100%;
  justify-content: space-between;
  margin-top: 0.3em;
}

#sim-nav-fr {
  width: 72%;
  justify-content: space-between;
}

#sim-nav-world {
  width: 13%;
}

.sim-nav {
  width: 100%;
}

.sim-nav-categories {
  width: 100%;
  border-top: solid rgba(0, 0, 0, 0.2) 1px;
  justify-content: space-between;
  padding-top: 5px;
}

.sim-nav-category {
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  position: relative;
  display: inline-block;
  height: 45px;
}

.sim-nav-category .sim-nav-category-background {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border-radius: 5px;
  opacity: 0;
  transition: 1s;
}

.sim-nav-category:hover .sim-nav-category-background {
  opacity: 1;
}

.sim-nav-category .sim-nav-category-icon {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
}

.sim-nav-category .sim-nav-category-icon-helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.sim-nav-category img {
  width: 90%;
  max-width: 30px;
  display: inline-block;
  vertical-align: middle;
}

#sim-nav-fr .sim-nav-category {
  width: 16%;
}

#sim-nav-world .sim-nav-category {
  width: 95%;
}

#options {
  width: 12%;
  margin-top: 31px;
  background-color: #b0e0e6;
}


/* /// MAIN-BOX */
.sim-main-box {
  background-color: white;
  height: auto;
  scroll-behavior: smooth;
  overflow-y: auto;
  padding: 10px;
  height: calc(100% - 140px);
  width: 100%;
}

/* /// TITRES CATEGORIES */

.sim-categorie {
  padding: 1.5% 1em;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2%;
  height: auto;
  width: 100%;
  text-align: center;
}

.sim-categorie>div {
  width: 55%;
  justify-content: space-between;
  align-items: center;
}

.sim-jauge {
  width: 100%;
  height: 20px;
  margin: 5px 0;
}

.sim-categorie-emissions {
  font-size: 15px;
  line-height: 19px;
}

.sim-categorie-markers {
  /* justify-content: space-evenly; */
  align-items: baseline;
  width: 100%;
}

.sim-categorie-markers>div {
  align-items: center;
  width: 24%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MuiTooltip-tooltipArrow {
  background-color: blue;
}

.sim-categorie-markers>div>div {
  width: 100%;
}

/* 
.sim-categorie-markers > div > div:last-of-type {
  width: 70%;
} */

.sim-categorie-markers p {
  font-size: 11px;
  text-align: center;
}

.sim-emissions-2030 {
  color: #0b8c85;
}

.sim-emissions-2020 {
  color: #ff6868;
}

.sim-emissions-2020 span {
  font-size: 9px;
}

.sim-emissions-2030 span {
  font-size: 9px;
}

.sim-categorie-name {
  font-weight: bold;
  font-size: 23px;
  line-height: 32px;
  color: #163e59;
  text-align: left;
  width: 45%;
}

/* /// OPTIONS */

#optionsContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;
  z-index: 9999;
}

#optionsContainer .optionsContainerBackground {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.5);
}

#optionsContainer .optionsContainerClose {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 2rem;
  cursor: pointer;
}

#scrollOptions {
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  color: #163E59;
}

#scrollOptions>div {
  justify-content: center;
}

.sim-options {
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em 0;
}

.sim-option-box {
  width: 90%;
  margin-bottom: 30px;

}

.sim-option-box p {
  margin-bottom: 0.5em;
  font-size: 0.8em;
}

.sim-option-box label {
  font-size: 1em;
  margin-right: 0.5em;
}

.sim-option-form {
  justify-content: flex-start;
}

.sim-option-form div {
  height: 2.2em;
  align-items: center;
  margin-right: 20px;
}



.sticky {
  margin-right: -0.3em;
  z-index: 2;
  background-color: #B0E0E6;
  position: sticky;
  position: -webkit-sticky;
  padding-bottom: 20px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 800px;
  margin: auto;
  text-align: left;
  margin-top: 150px;
}

.sticky .sim-categorie {
  background: #B0E0E6;
}

.sim-param-box {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin-bottom: 2%;
}

.sim-param-radio {
  margin-right: 1em;
}

input[type="radio"] {
  margin-right: 1em;
}

.bigger {
  height: 2.2em;
}

/* /// JAUGES */
.jauge-ext {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.jauge-int {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  /* background-image:linear-gradient(80deg, #7FFFD4 0%,  #88F2B6 18%, #77D9B5 38%, #1087A1 100%); */
}

.jauge-int-max {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.marker1,
.marker2 {
  z-index: 3;
}

/* /// RESULTS */
/* /// PROJECTION MONDIALE */
#results-top-box {
  background-color: white;
  height: 180px;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
}

#results-impacts-box {
  width: 100%;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 0.5em;
}

.n1 {
  grid-area: title1;
  text-align: center;
  align-self: center;
  margin: 0;
}

.n4 {
  grid-area: title2;
  text-align: center;
  align-self: center;
  margin: 0;
}

.n7 {
  grid-area: title3;
  text-align: center;
  align-self: center;
  margin: 0;
}

.n2 {
  grid-area: tag1;
  justify-self: center;
  margin: 0;
}

.n5 {
  grid-area: tag2;
  justify-self: center;
  margin: 0;
}

.n8 {
  grid-area: tag3;
  justify-self: center;
  margin: 0;
}

.n3 {
  grid-area: legend1;
  text-align: center;
  margin: 0;
}

.n6 {
  grid-area: legend2;
  text-align: center;
  margin: 0;
}

.n9 {
  grid-area: legend3;
  text-align: center;
  margin: 0;
}

#results-impacts-box {
  display: grid;
  width: 100%;
  margin: 0;
  grid: auto auto auto / 1fr 1fr 1fr;
  grid-gap: 2px 10px;
  gap: 2px 10px;
  grid-template-areas:
    "title1 title2 title3"
    "tag1 tag2 tag3"
    "legend1 legend2 legend3";
  margin-top: 0.5em;
}

/* /// PROJECTION FRANCAISE */

#results-bottom-box {
  background-color: white;
  height: calc(100% - 190px);
  width: 100%;
  padding: 10px;
  justify-content: space-between;
}

#results-emissions {
  width: 100%;
  align-items: center;
}

#results-emissions-charts-container {
  height: 55%;
  width: 100%;
  display: grid;
  grid: 85% 13% / 46% 46%;
  grid-gap: 5px 20px;
  gap: 5px 20px;
  grid-template-areas:
    "g1 g3"
    "g2 g4";
}

.g1 {
  grid-area: g1;
  width: 100%;
  height: 100%;
}

.g3 {
  grid-area: g3;
  width: 100%;
  height: 100%;
}

.g2 {
  grid-area: g2;
  text-align: center;
}

.g4 {
  grid-area: g4;
  text-align: center;
}

#results-emissions-charts-container p {
  text-align: center;
  font-size: 1.1em;
}

.chart {
  width: 100%;
  height: 100%;
}

#area-tooltip {
  padding: 10px;
  width: 350px;
  align-items: flex-start;
}

#area-tooltip .legend-point {
  margin-left: 20px;
  min-width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 5px;
}

#area-tooltip>div {
  align-items: center;
}

.chart-tooltip {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid lightgrey;
  box-shadow: 3px 3px 3px #163E59;
  font-size: 0.8em;
}

.chart-tooltip h4 {
  font-size: 1.1em;
  align-self: center;
}

.chart-tooltip p {
  font-size: 0.9em;
  align-self: center;
}


/* /// COMPOSANT INDICATEUR AVEC TITRE BOX ET LEGENDE */
#results-impacts-box2 {
  display: grid;
  width: 100%;
  margin: 0;
  grid: auto auto auto / 1fr 1.2fr 1fr;
  grid-gap: 2px;
  gap: 2px;
  grid-template-areas:
    "title4 title5 title6"
    "tag4 tag5 tag6"
    "legend4 legend5 legend6";
  margin-top: 0.5em;
}

.b1 {
  grid-area: title4;
  text-align: center;
  align-self: center;
  margin: 0;
}

.b4 {
  grid-area: title5;
  text-align: center;
  align-self: center;
  margin: 0;
}

.b7 {
  grid-area: title6;
  text-align: center;
  align-self: center;
  margin: 0;
}

.b2 {
  grid-area: tag4;
  justify-self: center;
  margin: 0;
}

.b5 {
  grid-area: tag5;
  justify-self: center;
  text-align: center;
  margin: 0;
}

.b8 {
  grid-area: tag6;
  justify-self: center;
  margin: 0;
}

.b3 {
  grid-area: legend4;
  text-align: center;
  margin: 0;
}

.b6 {
  grid-area: legend5;
  text-align: center;
  margin: 0;
}

.b9 {
  grid-area: legend6;
  text-align: center;
  margin: 0;
}

.results-title {
  font-size: 0.9em;
}

.results-legend {
  font-size: 0.7em;
  padding: 0 20px;
  text-align: center;
  justify-self: center;
}

.results-figure {
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  font-weight: bold;
  border-radius: 5px;
  line-height: normal;
  width: 5em;
  height: 3rem;
}

#results-impacts-box2 .results-figure {
  font-size: 1.5rem;
  /* padding: 0.1em 1.2em; */
}

.figure-unit {
  font-size: 0.7em;
  font-weight: lighter;
  margin-top: -0.2em;
}

#results-button {
  height: auto;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: white;
}

.sim-results-box button {
  padding: 0.5em 2em;
  font-size: 1.4em;
}

@media screen and (max-width: 1110px) {
  body {
    font-size: 3px;
  }

  .sim-container-box {
    width: 50%;
  }

  .sim-results-box {
    width: calc(50% - 10px);
  }
}

@media screen and (min-height: 1100px) {
  #results-emissions-charts-container {
    grid: 40% 6% 40% 6% / 60%;
    grid-gap: 5%;
    gap: 5%;
    justify-content: center;
    grid-template-areas:
      "g1"
      "g2"
      "g3"
      "g4";
  }
}

@media screen and (max-height: 700px) {

  .sim-categorie-emissions {
    font-size: 11px;
  }

  #results-top-box {
    height: 150px;
  }

  #results-bottom-box {
    height: calc(100% - 150px);
  }

  #sim-nav-box h1,
  #results-top-box h1,
  #results-emissions h1 {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 0;
  }

  .results-title {
    font-size: 0.8em;
  }

  .results-figure {
    font-size: 1.2em;
    width: 5em;
    height: 2.5rem;
    line-height: 2.5rem;
  }

  .b5 {
    line-height: normal;
  }

  #results-impacts-box2 .results-figure {
    font-size: 1.2rem;
  }

  .results-legend {
    font-size: 0.6em;
  }

  .b9 {
    font-size: 0.5em;
  }


  #results-emissions-charts-container {
    height: 45%;
    grid-gap: 2px 15px;
  }

  #results-emissions-charts-container p {
    font-size: 1em;
  }

  .sim-results-box button {
    padding: 0.4em 2em;
    font-size: 1.2em;
  }

}
.icon-box{
    border: none;
}

.icon-gears{
    font-size: 3em;
}
.results-page {
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
}



.results-page h1 {
    color: #7fffd4;
    font-size: 3em;
    border-bottom: #7fffd4 solid 4px;
    margin-bottom: 30px;
}


.results-page h2 {
    color: #7fffd4;
    font-size: 2em;
    align-self: flex-start;
}

.results-page h3 {
    font-size: 1.5em;
    align-self: flex-start;
}

.results-page .contact-white {
    width: 65%;
}

.results-page .contact-white p {
    font-size: 1.2em;
}


/* BLINKING BTN */
#blinking-results {
    position: -webkit-sticky;
    position: sticky;
    top: 2vh;
    align-self: flex-end;
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite;
    color: var(--lightgreen);
    font-size: 4em;
    border: none;
}

/* /// HERO RESULTS */

.chapter-selection:hover span {
    color: #7fffd4;
    border-bottom: 5px solid #7fffd4;
}

.results-box {
    font-size: 1em;
    width: 80%;
    height: 70%;
    padding: 2em 3em;
    grid-template-columns: 0.8fr 0.9fr 1.3fr;
    align-items: flex-start;
    border-radius: 1em;
}

.results-left {
    margin-top: 0.3em;
    height: 95%;
    justify-content: space-between;
    align-items: flex-start;
}

.results-left h3 {
    margin-bottom: 1em;
}

.results-left h5 {
    margin-bottom: 2em;
}

#results-impacts {
    justify-content: flex-end;
}

.sim-results-head-results h5 {
    width: 100%;
    font-size: 1.5em;
    text-align: left;
}

.sim-results-head-results p {
    font-size: 1em;
}

.results-figure-results {
    background-color: var(--grey);
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    font-weight: bold;
    height: 3rem;
    width: 5em;
    border-radius: 0.5rem;
    margin-bottom: 0.2rem;
    margin-right: 2em;
}

.results-data {
    margin-top: 0.3em;
    width: 90%;
    height: 90%;
    justify-content: space-between;
    align-items: center;
    margin-left: 1em;
    padding-left: 1em;
}

.results-data p {
    height: 5%;
    text-align: center;
}

.results-data-sunburst {
    width: 100%;
    height: 100%;
}

.results-data-area {
    width: 100%;
    height: 100%;
}

.results-btns {
    width: 80%;
    justify-content: space-between;
}

.share-icon {
    height: 32px;
}

/* ////////////////////////// */
/* /// SYNTHESE */
/* ////////////////////////// */

#hero-article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}


#res-synthese {
    flex: 1 1;
    margin-top: 5vh;
    width: 100%;
    align-items: center;
}

#res-synthese>div {
    width: 90%;
    padding: 20px;
    background-color: #442D59;
}

#res-synthese>div>* {
    justify-content: center;
    text-align: center;
}

#res-synthese-indicator {
    align-items: flex-start;
}

.tag-container {
    align-items: center;
    width: 30%;
}

#res-synthese>div>p {
    color: #7fffd4;
    margin: 30px 0;
    font-size: 1.5em;
    margin-left: 15%;
    width: 70%;
}

#res-synthese button {
    border: none;
    color: #34244E
}

#res-synthese-buttons>div {
    background-color: white;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    color: #34244E;
    align-items: center;
    justify-content: center;
    display: flex;
}

#res-synthese-buttons div:first-child {
    margin-right: 1em;
}


/* ////////////////////////// */
/* /// FRANCE */
/* ////////////////////////// */


/* /// PIE */
#res-emi-fr {
    width: 80%;
    align-items: center;
}

.res-emi-fr-container {
    min-height: 100vh;
    width: 100%;
    margin: 3vh 0;
}

.res-title-box {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    margin-bottom: 30px;
}

.chart-short-desc {
    font-size: 1.1em;
    margin-bottom: 20px;
    font-style: italic;
}

.res-chart-container {
    width: 100%;
}

.res-chart {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 55%;
    height: 75vh;
}

.res-chart img {
    max-width: 98%;
}

.res-chart-infos {
    width: 45%;
    justify-content: space-around;
    padding-left: 15px
}

.res-chart-infos p {
    font-size: 0.9em;
}

.res-chart-legend {
    flex-wrap: wrap;
}

.res-chart-legend>div {
    align-items: center;
    margin-right: 5%;
}

.res-chart-legend>div div {
    padding: 5px 10px 5px 0;
}

.res-chart-legend>p {
    margin-bottom: 10px;
}

.res-chart-legend td {
    padding: 5px 10px 5px 0;
}

.legend-point {
    min-width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
}

.res-chart-source {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    font-size: 0.9em;
}


/* ////////////////////////// */
/* /// MONDE */
/* ////////////////////////// */

#res-emi-world {
    width: 80%;
    align-items: center;
}

/* ////////////////////////// */
/* /// IMPACTS */
/* ////////////////////////// */

#res-impacts {
    width: 80%;
    align-items: center;
}

#res-impacts .res-chart {
    width: 60%;
}

#res-impacts .res-chart-infos {
    width: 38%;
}


/* /// DETAIL RESULTS */
.detail-results {
    position: relative;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0;
}

.top-btn {
    margin-bottom: 4em;
}

.detail-national {
    align-items: center;
    justify-content: center;
    margin-top: 2em;
    margin: 2em 0 3em;
    width: 90%;
}

.detail-national-main {
    width: 90%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-items: stretch;
    justify-items: center;
    border-radius: 1em;
    padding: 1em;
    margin-top: 1em;
}

.detail-national-box {
    width: 90%;
    height: 80%;
    align-items: center;
}

.detail-national-box h4 {
    margin-bottom: 1em;
    text-align: center;
}

.detail-impacts-temperature {
    align-items: center;
    width: 90%;
    border-radius: 1em;
    padding: 1em;
    margin-top: 1em;
}

.detail-impacts-temperature img {
    width: 70%;
    height: auto;
    margin-bottom: 3em;
}

.detail-world {
    padding: 1em;
    border-radius: 1em;
    margin: 1em 0 2em;
    width: 50%;
    height: 30em
}

.detail-world>h2 {
    text-align: center
}


.detail-parameters {
    align-items: center;
    justify-content: space-between;
    margin-top: 2em;
    width: 100%;
}

.detail-parameters-box {
    width: 90%;
    grid-template-columns: repeat(2, minmax(375px, 1fr));
    grid-column-gap: 2em;
    -webkit-column-gap: 2em;
            column-gap: 2em;
    grid-row-gap: 1em;
    row-gap: 1em;
    border-radius: 1em;
    padding: 1em;
    margin: 1em 0 2em;
}


/* FORM SAVE */

.modal-div {
    background-color: #07163A;
}

.new-or-edit {
    background-color: #07163A;
    align-items: center;
    display: flex;
    justify-content: space-around;
    padding: 1em 4em;
}

.new-or-edit>*>* {
    margin: 0 5px;
}

.form-popup {
    background-color: #07163A;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1em 0;
}

.form-popup>* {
    margin-bottom: 1em;
}

.popup-error {
    background-color: #07163A;
    text-align: center;
    padding: 1em;
    color: white;
}

.popup-error a {

    font-weight: bold;
    margin-right: 0.3em;
}


/* BLINKING BTN */
.blinking {
    -webkit-animation: blinker 2s linear infinite;
            animation: blinker 2s linear infinite;
}

@-webkit-keyframes blinker {
    50% {
        opacity: 0;
    }
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

